.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
h1 {
  margin-top: -30px;
  margin-bottom: 30px;
  font-size: 20px;
  font-weight: 500;
  color: #2196f3;
}
article {
  display: block;
  padding: 30px 60px;
  background: #eee;
  border-radius: 8px;
}
.npm {
  display: inline-block;
  margin: 20px 0 0 0;
}
body {
  margin: 0;
  padding: 0;
  background: #fafafa;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  line-height: 1.6;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.87);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}
p {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  line-height: 1.6;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.87);
  text-align: left;
}
.modal-container {
  max-width: 600px;
  padding: 20px;
}
.modal-body {
  position: relative;
  display: inline-block;
  padding: 20px;
  background: #fff;
  border-radius: 4px;
}
.modal-body h3 {
  display: block;
  margin: 0 0 20px 0;
  text-align: left;
  font-weight: 500;
}
.modal-body p {
  color: rgba(0, 0, 0, 0.54);
  font-size: 15px;
  font-weight: 400;
}
.modal-body .button-bar {
  text-align: right;
}
.modal-body.formal {
  padding: 0;
}
.modal-body.formal h3 {
  padding: 20px 15px 0 15px;
}
.modal-body.formal .body-wrap {
  padding: 15px;
  border-top: solid 1px #ddd;
  border-bottom: solid 1px #ddd;
}
.modal-body.formal p {
  color: rgba(0, 0, 0, 0.75);
}
.modal-body.formal .button-bar {
  padding: 15px;
}
.modal-body .close {
  display: inline-block;
  width: 40px;
  height: 40px;
  border: none;
  cursor: pointer;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 10px;
  opacity: .4;
  background: transparent;
  transition: background .2s ease;
  border-radius: 50%;
}
.modal-body .close:hover {
  background: #ddd;
}
@media (min-width: 768px) {
  .modal-body {
    min-width: 400px;
  }
}
